import cn from 'clsx';
import memoize from 'nano-memoize';
import equals from 'react-fast-compare';

import { TBackdropBlur } from '../types/tailwind';
import type { ChopPrefix } from '../types/twind';
import { computePseudoClasses, WithPseudo } from './pseudo';
import type { EnhancerFn } from './types';

export type BackdropBlurVals = ChopPrefix<'backdrop-blur', TBackdropBlur>;

export type BackdropProps = WithPseudo<{
  backdropBlur?: BackdropBlurVals;
}>;

export const backdropPropNames: Array<keyof BackdropProps> = ['backdropBlur'];

export const backdropProps: EnhancerFn<BackdropProps> = (props: BackdropProps) => {
  const { backdropBlur, ...rest } = props;

  return {
    props: rest,
    className: _backdropProps(backdropBlur),
  };
};

const _backdropProps = memoize(
  (backdropBlur: BackdropProps['backdropBlur']) => {
    return cn(computePseudoClasses('backdrop-blur', backdropBlur));
  },
  { maxAge: Infinity, equals },
);
