import * as React from 'react';

import { PanelOwnProps } from './types';

export type PanelContextProps = Pick<PanelOwnProps, 'appearance' | 'hasChildPanels'>;

export const PanelContext = React.createContext<PanelContextProps>(null);

export const usePanelContext = () => {
  return React.useContext(PanelContext);
};
