import cn from 'clsx';
import memoize from 'nano-memoize';
import equals from 'react-fast-compare';

import type {
  TAlignContent,
  TAlignItems,
  TAlignSelf,
  TFlex,
  TFlexDirection,
  TFlexGrow,
  TFlexShrink,
  TFlexWrap,
  TJustifyContent,
  TJustifyItems,
  TJustifySelf,
} from '../types/tailwind';
import { ChopPrefix } from '../types/twind';
import { computePseudoClasses, WithPseudo } from './pseudo';
import { EnhancerFn } from './types';

export type FlexVals = ChopPrefix<'flex', TFlex>;
export type FlexDirectionVals = ChopPrefix<'flex', TFlexDirection>;
export type FlexWrapVals = ChopPrefix<'flex', TFlexWrap, 'wrap'>;
export type FlexGrowVals = ChopPrefix<'grow', TFlexGrow>;
export type FlexShrinkVals = ChopPrefix<'shrink', TFlexShrink>;
export type JustifyContentVals = ChopPrefix<'justify', TJustifyContent>;
export type JustifyItemsVals = ChopPrefix<'justify-items', TJustifyItems>;
export type JustifySelfVals = ChopPrefix<'justify-self', TJustifySelf>;
export type AlignContentVals = ChopPrefix<'content', TAlignContent>;
export type AlignItemsVals = ChopPrefix<'items', TAlignItems>;
export type AlignSelfVals = ChopPrefix<'self', TAlignSelf>;

export type IFlexProps = WithPseudo<{
  flex?: FlexVals;
  flexDirection?: FlexDirectionVals;
  flexWrap?: FlexWrapVals;
  flexGrow?: FlexGrowVals;
  flexShrink?: FlexShrinkVals;
  justifyContent?: JustifyContentVals;
  justifyItems?: JustifyItemsVals;
  justifySelf?: JustifySelfVals;
  alignContent?: AlignContentVals;
  alignItems?: AlignItemsVals;
  alignSelf?: AlignSelfVals;
}>;

export type IFlexShorthandProps = {
  align?: IFlexProps['alignItems'];
  justify?: IFlexProps['justifyContent'];
  wrap?: IFlexProps['flexWrap'];
  direction?: IFlexProps['flexDirection'];
  grow?: IFlexProps['flexGrow'];
  shrink?: IFlexProps['flexShrink'];
};

export const flexPropNames: Array<keyof IFlexProps> = [
  'flex',
  'flexDirection',
  'flexWrap',
  'flexGrow',
  'flexShrink',
  'justifyContent',
  'justifyItems',
  'justifySelf',
  'alignContent',
  'alignItems',
  'alignSelf',
];

export const flexProps: EnhancerFn<IFlexProps> = (props: IFlexProps) => {
  const {
    flex,
    flexDirection,
    flexWrap,
    flexGrow,
    flexShrink,
    justifyContent,
    justifyItems,
    justifySelf,
    alignContent,
    alignItems,
    alignSelf,
    ...rest
  } = props;

  return {
    props: rest,
    className: _flexProps(
      flex,
      flexDirection,
      flexWrap,
      flexGrow,
      flexShrink,
      justifyContent,
      justifyItems,
      justifySelf,
      alignContent,
      alignItems,
      alignSelf,
    ),
  };
};

const _flexProps = memoize(
  (
    flex: IFlexProps['flex'],
    flexDirection: IFlexProps['flexDirection'],
    flexWrap: IFlexProps['flexWrap'],
    flexGrow: IFlexProps['flexGrow'],
    flexShrink: IFlexProps['flexShrink'],
    justifyContent: IFlexProps['justifyContent'],
    justifyItems: IFlexProps['justifyItems'],
    justifySelf: IFlexProps['justifySelf'],
    alignContent: IFlexProps['alignContent'],
    alignItems: IFlexProps['alignItems'],
    alignSelf: IFlexProps['alignSelf'],
  ) => {
    return cn(
      computePseudoClasses('flex', flex),
      computePseudoClasses('flex', flexDirection),
      computePseudoClasses('flex', flexWrap),
      computePseudoClasses('flex-grow', flexGrow),
      computePseudoClasses('flex-shrink', flexShrink),
      computePseudoClasses('justify', justifyContent),
      computePseudoClasses('justify-items', justifyItems),
      computePseudoClasses('justify-self', justifySelf),
      computePseudoClasses('content', alignContent),
      computePseudoClasses('items', alignItems),
      computePseudoClasses('self', alignSelf),
    );
  },
  { maxAge: Infinity, equals },
);
