import cn from 'clsx';
import { Provider as JotaiProvider } from 'jotai';
import memoize from 'nano-memoize';
import React, { forwardRef, useEffect, useRef } from 'react';
import equals from 'react-fast-compare';

import { usePrevious } from '../../hooks';
import {
  ThemeProviderContext,
  ThemeScope,
  useComputedTheme,
  useCustomTheme,
  useCustomThemeAtom,
  useThemeId,
  useThemeIdAtom,
  useUpdateTheme,
} from '../../hooks/use-theme';
import { ComputedTheme, ICustomTheme, PrebuiltThemeIds, ThemeMode, tw, twCss } from '../../utils';

export type ThemeProviderProps = {
  children: JSX.Element;
  baseThemeId?: PrebuiltThemeIds;
  theme?: ICustomTheme;
  mode?: ThemeMode;
  scope?: ThemeScope;
  isInverted?: boolean;
};

export const ThemeProvider = forwardRef(({ baseThemeId, theme, scope, ...props }: ThemeProviderProps, ref) => {
  const parentThemeId = useThemeId();
  const parentTheme = useCustomTheme();
  const themeIdAtom = useThemeIdAtom();
  const customThemeAtom = useCustomThemeAtom();

  return (
    <ThemeProviderContext.Provider value={scope}>
      <JotaiProvider
        scope={scope}
        initialValues={[
          [themeIdAtom, baseThemeId || parentThemeId],
          [customThemeAtom, theme || parentTheme],
        ]}
      >
        <ThemeProviderInner {...props} theme={theme} scope={scope} ref={ref} />
      </JotaiProvider>
    </ThemeProviderContext.Provider>
  );
});

const ThemeProviderInner = forwardRef(
  (
    { children, scope, theme, isInverted }: Pick<ThemeProviderProps, 'children' | 'scope' | 'theme' | 'isInverted'>,
    ref,
  ) => {
    const computed = useComputedTheme(scope);
    const updateTheme = useUpdateTheme(scope);

    const _className = cn(children.props.className, themeClassFn(computed.css));

    /**
     * If a new theme was passed in, update it.
     */
    const prevTheme = usePrevious(theme);
    useEffect(() => {
      if (!equals(theme, prevTheme)) {
        updateTheme(theme);
      }
    }, [prevTheme, theme, updateTheme]);

    return React.cloneElement(children, {
      className: _className || undefined,
      ref,
      'data-theme': isInverted ? 'inverted' : computed.theme.isDark ? 'dark' : 'light',
    });
  },
);

const IS_SERVER = typeof document === 'undefined';
const computeThemeClass = (css: ComputedTheme['css']) => tw(twCss(css));
const memoizedComputeThemeClass = memoize(computeThemeClass, { maxAge: Infinity });
const themeClassFn = IS_SERVER ? computeThemeClass : memoizedComputeThemeClass;
