import cn from 'clsx';
import memoize from 'nano-memoize';
import equals from 'react-fast-compare';

import { THeight, TMaxHeight, TMaxWidth, TMinHeight, TMinWidth, TWidth } from '../types/tailwind';
import type { ChopPrefix } from '../types/twind';
import { computePseudoClasses, WithPseudo } from './pseudo';
import type { EnhancerFn } from './types';

export type HeightVals = ChopPrefix<'h', THeight>;
export type MaxHeightVals = ChopPrefix<'max-h', TMaxHeight>;
export type MinHeightVals = ChopPrefix<'min-h', TMinHeight>;

export type WidthVals = ChopPrefix<'w', TWidth>;
export type MaxWidthVals = ChopPrefix<'max-w', TMaxWidth>;
export type MinWidthVals = ChopPrefix<'min-w', TMinWidth>;

export type IHeightProps = WithPseudo<{
  h?: HeightVals;
  maxH?: MaxHeightVals;
  minH?: MinHeightVals;
}>;

export type IWidthProps = WithPseudo<{
  w?: WidthVals;
  maxW?: MaxWidthVals;
  minW?: MinWidthVals;
}>;

export type ISizeProps = IHeightProps & IWidthProps;

export const sizePropNames: Array<keyof ISizeProps> = ['h', 'maxH', 'minH', 'maxW', 'minW', 'w'];

export const sizeProps: EnhancerFn<ISizeProps> = (props: ISizeProps) => {
  const { h, maxH, minH, w, maxW, minW, ...rest } = props;

  return { props: rest, className: _sizeProps(h, maxH, minH, w, maxW, minW) };
};

const _sizeProps = memoize(
  (
    h: ISizeProps['h'],
    maxH: ISizeProps['maxH'],
    minH: ISizeProps['minH'],
    w: ISizeProps['w'],
    maxW: ISizeProps['maxW'],
    minW: ISizeProps['minW'],
  ) => {
    return cn(
      computePseudoClasses('w', w),
      computePseudoClasses('max-w', maxW),
      computePseudoClasses('min-w', minW),
      computePseudoClasses('h', h),
      computePseudoClasses('max-h', maxH),
      computePseudoClasses('min-h', minH),
    );
  },
  { maxAge: Infinity, equals },
);
