import cn from 'clsx';
import * as React from 'react';
import { CSSProperties, forwardRef, memo } from 'react';

import { HeightVals, IMarginProps, WidthVals } from '../../enhancers';
import { IBoxHTMLAttributes } from '../Box/types';
import { Flex } from '../Flex';
import { Icon, IIconProps } from '../Icon';
import { Text } from '../Text';

const sizes: Record<'sm' | 'md' | 'lg', [HeightVals, WidthVals]> = {
  lg: ['md', 8],
  md: ['sm', 6],
  sm: ['xs', 5],
};

type BaseAvatarProps = IMarginProps & {
  size?: 'sm' | 'md' | 'lg';

  /**
   * @deprecated Prefer build in style props or the `tw` prop, and as last resort `UNSAFE_className`. For removal in v3.
   */
  className?: string;

  bg?: CSSProperties['backgroundColor'];
};

export type ImageAvatarProps = BaseAvatarProps & {
  imageUrl: string;
};

export type IconAvatarProps = BaseAvatarProps & {
  icon: IIconProps['icon'];
};

export type LetterAvatarProps = BaseAvatarProps & {
  letter: string;
};

export type BlankAvatarProps = BaseAvatarProps & {
  blank: true;
};

export type AvatarProps = ImageAvatarProps | IconAvatarProps | LetterAvatarProps | BlankAvatarProps;

export const Avatar = memo(
  forwardRef<HTMLDivElement, AvatarProps>(function Avatar(props, ref) {
    // TODO: the union types make spreading out rest and passing through to box difficult, since we can't
    // easily pull out letter, blank, etc. they're getting sent to box right now...
    // @ts-expect-error
    let { className, size = 'md', bg, icon, letter, blank, ...rest } = props;

    let extraProps: IBoxHTMLAttributes = {};
    if (bg) {
      extraProps.style = {
        color: 'white',
        backgroundColor: bg,
      };
    }

    let innerElem;
    if (icon) {
      innerElem = <Icon icon={icon} />;
    } else if (letter) {
      innerElem = <Text>{letter}</Text>;
    }

    return (
      <Flex
        ref={ref}
        className={cn('sl-avatar', className)}
        align="center"
        justify="center"
        h={sizes[size][0]}
        w={sizes[size][1]}
        rounded="full"
        {...rest}
        {...extraProps}
      >
        {innerElem}
      </Flex>
    );
  }),
);
