import { CX, twApply, twCss, twStyle } from '../../utils';
import { linkStyle } from '../Link';

export const iconStyles = twApply`opacity-0 ml-4 text-base text-muted`;

export const linkHeadingStyle = twStyle({
  base: twCss({
    '&:hover': {
      '& .sl-link-heading__icon': {
        '@apply': CX('opacity-100'),
      },
    },

    [`& ${linkStyle.selector}`]: {
      '@apply': CX.$combine(CX('text-heading', 'items-center', 'inline-flex'), CX.hover('hover:text-heading')),
    },
  }),
});
