import cn from 'clsx';

import { ChopSuffix, KebabToCamelCase, TPseudoClassVariants } from '../types/twind';

type ThemeVariant = 'dark' | 'inverted';
type ResponsiveVariant = 'sm' | 'md' | 'lg';
type ComplexVariant = ThemeVariant | ResponsiveVariant;

type TwindPseudoVariant = ChopSuffix<':', TPseudoClassVariants>;
type MosaicPseudoVariant = Exclude<KebabToCamelCase<TwindPseudoVariant>, ComplexVariant>;

type PseudoMapping<T> = {
  [key in MosaicPseudoVariant | 'default']?: T;
};

export type WithPseudo<P extends Object> = {
  [K in keyof P]?: P[K] | Pseudo<P[K]>;
};

export type Pseudo<T> = PseudoMapping<T> & {
  [key in ComplexVariant]?:
    | T
    | {
        [key in MosaicPseudoVariant]?: T;
      };
};

const MosaicToTwindPseudoMap: Record<MosaicPseudoVariant, TwindPseudoVariant> = {
  active: 'active',
  after: 'after',
  before: 'before',
  checked: 'checked',
  disabled: 'disabled',
  even: 'even',
  first: 'first',
  firstOfType: 'first-of-type',
  focus: 'focus',
  focusWithin: 'focus-within',
  group: 'group',
  groupFocus: 'group-focus',
  groupHover: 'group-hover',
  hover: 'hover',
  invalid: 'invalid',
  last: 'last',
  lastOfType: 'last-of-type',
  odd: 'odd',
  only: 'only',
  onlyOfType: 'only-of-type',
  placeholder: 'placeholder',
  required: 'required',
  valid: 'valid',
  visited: 'visited',
};

const MosaicToTwindPsuedoEntries = Object.entries(MosaicToTwindPseudoMap);

const isPseudoMappingObject = (obj: any): obj is PseudoMapping<boolean | string | number> =>
  obj && typeof obj === 'object';
const isPseudoObject = (obj: any): obj is Pseudo<boolean | string | number> => obj && typeof obj === 'object';

export const isNegative = (v: boolean | string | number) => {
  return v < 0 || v === '-px';
};

export const buildClassname = (p: string, v: boolean | string | number) => {
  if (v === true) return `${p}`;

  const isNeg = isNegative(v);
  return `${isNeg ? '-' : ''}${p}${!p || isNeg ? '' : '-'}${v}`;
};

export const computeVariantClasses = (
  prop: string,
  val: boolean | string | number | PseudoMapping<boolean | string | number>,
  variant?: ComplexVariant,
) => {
  let classes = {};

  const prefix = variant ? `${variant}:` : '';

  // if val is literal true value, then we're trying to use default and do not need to append a value
  // val can be 0, account for this
  if (isPseudoMappingObject(val)) {
    if (val.default || val.default === 0) {
      classes[buildClassname(prop, val.default)] = true;
    }

    for (const [mosaicKey, twindKey] of MosaicToTwindPsuedoEntries) {
      if (val[mosaicKey] || val[mosaicKey] === 0) {
        classes[`${prefix}${twindKey}:${buildClassname(prop, val[mosaicKey])}`] = true;
      }
    }
  } else {
    if (val || val === 0) {
      classes[buildClassname(`${prefix}${prop}`, val)] = true;
    }
  }

  return classes;
};

export const computePseudoClasses = (
  prop: string,
  val: boolean | string | number | Pseudo<boolean | string | number>,
) => {
  let classes = {};

  if (isPseudoObject(val)) {
    const { sm, md, lg, dark, inverted, ...rest } = val;

    classes = {
      ...computeVariantClasses(prop, rest),
      ...computeVariantClasses(prop, dark, 'dark'),
      ...computeVariantClasses(prop, inverted, 'inverted'),
      ...computeVariantClasses(prop, sm, 'sm'),
      ...computeVariantClasses(prop, md, 'md'),
      ...computeVariantClasses(prop, lg, 'lg'),
    };
  } else {
    if (val || val === 0) {
      classes[buildClassname(prop, val)] = true;
    }
  }

  return cn(classes);
};
