import * as React from 'react';

import { Box, BoxOwnProps } from '../Box';
import { usePanelContext } from './PanelContext';

export const PanelContent: React.FC<BoxOwnProps> = ({ children, ...props }) => {
  const { appearance, hasChildPanels } = usePanelContext();
  const isMinimal = appearance === 'minimal';
  const isOutlined = appearance === 'outlined';

  return (
    <Box
      p={4}
      className="sl-panel__content"
      bg={!isMinimal && !isOutlined && !hasChildPanels ? { default: 'canvas-50', inverted: 'transparent' } : undefined}
      {...props}
    >
      {children}
    </Box>
  );
};
