import memoize from 'nano-memoize';
import * as React from 'react';
import { forwardRef } from 'react';

import {
  backdropProps,
  borderProps,
  colorProps,
  EnhancerFn,
  flexProps,
  interactivityProps,
  layoutProps,
  marginProps,
  paddingProps,
  positionProps,
  ringProps,
  shadowProps,
  sizeProps,
  transformProps,
  typographyProps,
} from '../../enhancers';
import { twCss, tx } from '../../utils';
import { BoxOwnProps, BoxProps } from './types';

const defaultElement = 'div';

export const Box: <E extends React.ElementType = typeof defaultElement>(props: BoxProps<E>) => JSX.Element = forwardRef(
  function Box(
    { as, className, UNSAFE_className, role, noFocusRing, styled, css, ...props }: BoxOwnProps & { role?: string },
    ref: React.Ref<Element>,
  ) {
    let classNames: string = '';

    // custom pipe to just make accumulating the generated classNames, and pulling out props, easier
    const pipe =
      (...fns: EnhancerFn<BoxOwnProps>[]) =>
      x =>
        fns.reduce((v, f) => {
          const { props, className } = f(v);
          classNames += ` ${className}`;
          return props;
        }, x);

    // run the props through all the enhancers - what we're left with is the extra props that we're not specifically handling
    // these get passed on to the underlying Element as is
    const restProps = pipe(
      backdropProps,
      layoutProps,
      flexProps,
      positionProps,
      sizeProps,
      typographyProps,
      marginProps,
      paddingProps,
      colorProps,
      borderProps,
      ringProps,
      shadowProps,
      interactivityProps,
      transformProps,
    )(props);

    let _css;
    if (css) {
      _css = twCss(css);
    }

    const cleansedClassnames = cleanClassnames(classNames);
    const propClassNames = cleansedClassnames.trim();

    const Element = as || defaultElement;

    return (
      <Element
        ref={ref}
        {...restProps}
        role={role}
        className={
          tx(styled, className, UNSAFE_className, propClassNames, {
            'sl-group': role === 'group',
            'sl-no-focus-ring': noFocusRing,
          }) || undefined
        }
      />
    );
  },
);

// @ts-ignore
Box.displayName = 'Box';

// @ts-ignore
Box.defaultProps = {
  as: defaultElement,
};

const cleanClassnames = memoize(classNames => {
  return classNames
    .split('\n')
    .filter(v => {
      const value = v.trim();
      return !(value === 'false' || value === 'null' || value === 'undefined');
    })
    .join(' ');
});
