import { CX, twCss, twStyle } from '../../utils';

export const linkStyle = twStyle({
  base: twCss({
    '@apply': CX.$combine(CX('text-link'), CX.hover('hover:text-link-dark')),
    '& > code': {
      '@apply': CX('text-link'),
    },
    '&:hover > code': {
      '@apply': CX('text-link-dark'),
    },
  }),
});
