import chroma from 'chroma-js';

export type HslColor2 = { h: number; s: number; l: number; a?: number };

export const readableColor = (
  bg: chroma.Color,
  preferred: chroma.Color | string,
  fallback: chroma.Color | string,
): chroma.Color => {
  const preferredChroma = chroma(preferred);
  const preferredContrast = chroma.contrast(bg, preferredChroma);

  const fallbackChroma = chroma(fallback);
  const fallbackContrast = chroma.contrast(bg, fallbackChroma);

  const howMuchBetterIsFallbackContrast = fallbackContrast - preferredContrast;

  if (preferredContrast >= 3.5) return preferredChroma;

  return howMuchBetterIsFallbackContrast >= 1 ? fallbackChroma : preferredChroma;
};
