import cn from 'clsx';
import memoize from 'nano-memoize';
import equals from 'react-fast-compare';

import type {
  TBorderColor,
  TBorderRadius,
  TBorderWidth,
  TRingColor,
  TRingOpacity,
  TRingWidth,
} from '../types/tailwind';
import type { ChopPrefix, ChopPrefixAndModifier } from '../types/twind';
import { computePseudoClasses, WithPseudo } from './pseudo';
import type { EnhancerFn } from './types';

export type OutlineVals = 'none';

/*
 * Border
 */
type CornerPrefix = 't' | 'r' | 'b' | 'l';
export type BorderColorVals = ChopPrefixAndModifier<'border', CornerPrefix, TBorderColor> | boolean;
export type BorderWidthVals = ChopPrefixAndModifier<'border', CornerPrefix | 'x' | 'y', TBorderWidth>;
export type RoundedVals = ChopPrefix<'rounded', TBorderRadius>;

export type BorderWidthProps = WithPseudo<{
  border?: BorderWidthVals;
  borderT?: BorderWidthVals;
  borderR?: BorderWidthVals;
  borderL?: BorderWidthVals;
  borderB?: BorderWidthVals;
}>;

export type BorderRoundedProps = WithPseudo<{
  rounded?: RoundedVals;
  roundedT?: RoundedVals;
  roundedR?: RoundedVals;
  roundedL?: RoundedVals;
  roundedB?: RoundedVals;
  roundedTL?: RoundedVals;
  roundedTR?: RoundedVals;
  roundedBL?: RoundedVals;
  roundedBR?: RoundedVals;
}>;

export type BorderProps = WithPseudo<{
  outline?: OutlineVals;
  borderColor?: BorderColorVals;
}> &
  BorderWidthProps &
  BorderRoundedProps;

export const borderPropNames: Array<keyof BorderProps> = [
  'borderColor',
  'border',
  'borderT',
  'borderR',
  'borderL',
  'borderB',
  'rounded',
  'roundedT',
  'roundedR',
  'roundedL',
  'roundedB',
  'roundedTL',
  'roundedTR',
  'roundedBL',
  'roundedBR',
];

export const borderProps: EnhancerFn<BorderProps> = (props: BorderProps) => {
  const {
    outline,
    rounded,
    roundedB,
    roundedT,
    roundedR,
    roundedL,
    roundedBL,
    roundedBR,
    roundedTL,
    roundedTR,
    borderColor,
    border,
    borderT,
    borderR,
    borderL,
    borderB,
    ...rest
  } = props;

  return {
    props: rest,
    className: _borderProps(
      outline,
      rounded,
      roundedB,
      roundedT,
      roundedR,
      roundedL,
      roundedBL,
      roundedBR,
      roundedTL,
      roundedTR,
      borderColor,
      border,
      borderT,
      borderR,
      borderL,
      borderB,
    ),
  };
};

const _borderProps = memoize(
  (
    outline: BorderProps['outline'],
    rounded: BorderProps['rounded'],
    roundedB: BorderProps['roundedB'],
    roundedT: BorderProps['roundedT'],
    roundedR: BorderProps['roundedR'],
    roundedL: BorderProps['roundedL'],
    roundedBL: BorderProps['roundedBL'],
    roundedBR: BorderProps['roundedBR'],
    roundedTL: BorderProps['roundedTL'],
    roundedTR: BorderProps['roundedTR'],
    borderColor: BorderProps['borderColor'],
    border: BorderProps['border'],
    borderT: BorderProps['borderT'],
    borderR: BorderProps['borderR'],
    borderL: BorderProps['borderL'],
    borderB: BorderProps['borderB'],
  ) => {
    return cn(
      computePseudoClasses('outline', outline),
      computePseudoClasses('rounded', rounded),
      computePseudoClasses('rounded-b', roundedB),
      computePseudoClasses('rounded-t', roundedT),
      computePseudoClasses('rounded-r', roundedR),
      computePseudoClasses('rounded-l', roundedL),
      computePseudoClasses('rounded-bl', roundedBL),
      computePseudoClasses('rounded-br', roundedBR),
      computePseudoClasses('rounded-tl', roundedTL),
      computePseudoClasses('rounded-tr', roundedTR),
      computePseudoClasses('border', borderColor),
      computePseudoClasses('border', border),
      computePseudoClasses('border-t', borderT),
      computePseudoClasses('border-r', borderR),
      computePseudoClasses('border-l', borderL),
      computePseudoClasses('border-b', borderB),
    );
  },
  { maxAge: Infinity, equals },
);

/*
 * Ring
 */

export type RingWidthVals = ChopPrefix<'ring', TRingWidth>;
export type RingColorVals = ChopPrefix<'ring', TRingColor>;
export type RingOpacityVals = ChopPrefix<'ring-opacity', TRingOpacity>;

export type IRingProps = WithPseudo<{
  ring?: RingWidthVals;
  ringColor?: RingColorVals;
  ringOpacity?: RingOpacityVals;
}>;

export const ringPropNames: Array<keyof IRingProps> = ['ring', 'ringColor'];

export const ringProps: EnhancerFn<IRingProps> = (props: IRingProps) => {
  const { ring, ringColor, ringOpacity, ...rest } = props;

  return {
    props: rest,
    className: _ringProps(ring, ringColor, ringOpacity),
  };
};

const _ringProps = memoize(
  (ring: IRingProps['ring'], ringColor: IRingProps['ringColor'], ringOpacity: IRingProps['ringOpacity']) => {
    return cn(
      computePseudoClasses('ring', ring),
      computePseudoClasses('ring', ringColor),
      computePseudoClasses('ring-opacity', ringOpacity),
    );
  },
  { maxAge: Infinity, equals },
);
