import cn from 'clsx';
import memoize from 'nano-memoize';
import equals from 'react-fast-compare';

import type { TInset, TPosition, TZIndex } from '../types/tailwind';
import type { ChopPrefix, WithNegatives } from '../types/twind';
import { computePseudoClasses, WithPseudo } from './pseudo';
import type { EnhancerFn } from './types';

type PositionVals = TPosition;
// they are all the same, can just use one of them, such as `x`
type PinVals = true | WithNegatives<ChopPrefix<'inset-x', TInset>>;
type ZIndexVals = ChopPrefix<'z', TZIndex>;

export type IPositionProps = WithPseudo<{
  pos?: PositionVals;
  pin?: PinVals;
  pinY?: PinVals;
  pinX?: PinVals;
  top?: PinVals;
  left?: PinVals;
  right?: PinVals;
  bottom?: PinVals;
  zIndex?: ZIndexVals;
}>;

export const positionPropNames: Array<keyof IPositionProps> = [
  'bottom',
  'pin',
  'pinX',
  'pinY',
  'left',
  'pos',
  'right',
  'top',
  'zIndex',
];

export const positionProps: EnhancerFn<IPositionProps> = (props: IPositionProps) => {
  const { pos, pin, pinY, pinX, top, left, right, bottom, zIndex, ...rest } = props;

  return { props: rest, className: _positionProps(pos, pin, pinY, pinX, top, left, right, bottom, zIndex) };
};

const _positionProps = memoize(
  (
    pos: IPositionProps['pos'],
    pin: IPositionProps['pin'],
    pinY: IPositionProps['pinY'],
    pinX: IPositionProps['pinX'],
    top: IPositionProps['top'],
    left: IPositionProps['left'],
    right: IPositionProps['right'],
    bottom: IPositionProps['bottom'],
    zIndex: IPositionProps['zIndex'],
  ) => {
    return cn(
      computePseudoClasses('', pos),
      computePseudoClasses('inset', pin === true ? '0' : pin),
      computePseudoClasses('inset-y', pinY === true ? '0' : pinY),
      computePseudoClasses('inset-x', pinX === true ? '0' : pinX),
      computePseudoClasses('top', top),
      computePseudoClasses('left', left),
      computePseudoClasses('right', right),
      computePseudoClasses('bottom', bottom),
      computePseudoClasses('z', zIndex),
    );
  },
  { maxAge: Infinity, equals },
);
