import { CX, twCss } from '../../utils';

export const switchCss = twCss({
  '@apply': CX('relative', 'block', 'select-none'),

  '.sl-switch__indicator': {
    transition: 'background-color 0.1s cubic-bezier(0.4, 1, 0.75, 0.9)',

    '& .sl-switch__icon': {
      visibility: 'hidden',
    },

    '&::before': {
      '@apply': CX('bg-canvas'),
      content: '""',
      'border-radius': '50%',
      height: 'calc(100% - 4px)',
      width: 'calc(50% - 4px)',
      left: '0px',
      margin: '2px',
      position: 'absolute',
      transition: 'left 0.1s cubic-bezier(0.4, 1, 0.75, 0.9)',
    },
  },

  'input:checked': {
    '&:disabled': {
      '& ~ .sl-switch__indicator': {
        '@apply': CX('bg-primary-light'),
      },
    },

    '& ~ .sl-switch__indicator': {
      '@apply': CX('bg-primary'),

      '& .sl-switch__icon': {
        visibility: 'visible',
      },

      '&::before': {
        left: '50%',
      },
    },
  },
});
