import cn from 'clsx';
import memoize from 'nano-memoize';
import equals from 'react-fast-compare';

import { TGap, TMargin, TPadding } from '../types/tailwind';
import { ChopPrefix, WithNegatives } from '../types/twind';
import { computePseudoClasses, WithPseudo } from './pseudo';
import type { EnhancerFn } from './types';

export type MarginVals = WithNegatives<ChopPrefix<'m', TMargin>> | '-px';
export type PaddingVals = ChopPrefix<'p', TPadding>;
export type GapVals = ChopPrefix<'gap-x', TGap>;

export type IMarginProps = WithPseudo<{
  m?: MarginVals;
  mx?: MarginVals;
  my?: MarginVals;
  mt?: MarginVals;
  mr?: MarginVals;
  mb?: MarginVals;
  ml?: MarginVals;
}>;

export type IPaddingProps = WithPseudo<{
  p?: PaddingVals;
  px?: PaddingVals;
  py?: PaddingVals;
  pt?: PaddingVals;
  pr?: PaddingVals;
  pb?: PaddingVals;
  pl?: PaddingVals;
}>;

export type ISpacingProps = IMarginProps & IPaddingProps;

export const spacingPropNames: Array<keyof ISpacingProps> = [
  'm',
  'mb',
  'ml',
  'mr',
  'mt',
  'mx',
  'my',
  'p',
  'pb',
  'pl',
  'pr',
  'pt',
  'px',
  'py',
];

export const marginProps: EnhancerFn<IMarginProps> = (props: IMarginProps) => {
  const { m, mx, my, mt, mr, mb, ml, ...rest } = props;

  return { props: rest, className: _marginProps(m, mx, my, mt, mr, mb, ml) };
};

const _marginProps = memoize(
  (
    m: IMarginProps['m'],
    mx: IMarginProps['mx'],
    my: IMarginProps['my'],
    mt: IMarginProps['mt'],
    mr: IMarginProps['mr'],
    mb: IMarginProps['mb'],
    ml: IMarginProps['ml'],
  ) => {
    return cn(
      computePseudoClasses('m', m),
      computePseudoClasses('mx', mx),
      computePseudoClasses('my', my),
      computePseudoClasses('mt', mt),
      computePseudoClasses('mr', mr),
      computePseudoClasses('mb', mb),
      computePseudoClasses('ml', ml),
    );
  },
  { maxAge: Infinity, equals },
);

export const paddingProps: EnhancerFn<IPaddingProps> = (props: IPaddingProps) => {
  const { p, px, py, pt, pr, pb, pl, ...rest } = props;

  return { props: rest, className: _paddingProps(p, px, py, pt, pr, pb, pl) };
};

const _paddingProps = memoize(
  (
    p: IPaddingProps['p'],
    px: IPaddingProps['px'],
    py: IPaddingProps['py'],
    pt: IPaddingProps['pt'],
    pr: IPaddingProps['pr'],
    pb: IPaddingProps['pb'],
    pl: IPaddingProps['pl'],
  ) => {
    return cn(
      computePseudoClasses('p', p),
      computePseudoClasses('px', px),
      computePseudoClasses('py', py),
      computePseudoClasses('pt', pt),
      computePseudoClasses('pr', pr),
      computePseudoClasses('pb', pb),
      computePseudoClasses('pl', pl),
    );
  },
  { maxAge: Infinity, equals },
);
