import { CX, twCss } from '../../utils';

/* well known padding hack: https://css-tricks.com/aspect-ratio-boxes/ */

export const aspectRatioCSS = twCss({
  '&::before': {
    '@apply': CX('block', 'h-0'),

    content: '""',
    'padding-bottom': 'calc(1 / var(--ratio) * 100%)',
  },

  '& > *:not(style)': {
    '@apply': CX(
      'overflow-hidden',
      'absolute',
      'inset-0',
      'flex',
      'justify-center',
      'items-center',
      'w-full',
      'h-full',
    ),
  },

  '& > img, & > video': {
    '@apply': CX('object-cover'),
  },
});
