import { atom, WritableAtom } from 'jotai';
import type { Atom, Scope } from 'jotai/core/atom';
import { useAtomValue, useUpdateAtom } from 'jotai/utils';
import { createContext, useContext } from 'react';

import {
  ComputedTheme,
  computeTheme,
  defaultThemeMode,
  getResolvedThemeMode,
  ICustomTheme,
  PrebuiltThemeIds,
  ThemeMode,
} from '../utils/theme';

export type ThemeScope = Scope;
export const ThemeProviderContext = createContext<ThemeScope | null>(null);

/**
 * Resolved theme
 */

export const themeMode = atom<ThemeMode>(defaultThemeMode());
export const resolvedThemeModeAtom = atom<'light' | 'dark'>(get => {
  return getResolvedThemeMode(get(themeMode));
});
export const currentThemeIdAtom = atom<PrebuiltThemeIds>(get => {
  const mode = get(resolvedThemeModeAtom);
  return mode === 'light' ? get(lightThemeIdAtom) : get(darkThemeIdAtom);
});
export const customThemeAtom = atom<ICustomTheme>(get => {
  const mode = get(resolvedThemeModeAtom);
  return mode === 'light' ? get(customLightThemeAtom) : get(customDarkThemeAtom);
});
export const computedThemeAtom = atom<ComputedTheme>(get => {
  return computeTheme(get(currentThemeIdAtom), get(customLightThemeAtom));
});

export const useThemeId = (scope?: ThemeScope) => useThemeAtomValue(currentThemeIdAtom, scope);
export const useThemeIdAtom = (scope?: ThemeScope) => {
  const mode = useResolvedThemeMode(scope);
  return mode === 'light' ? lightThemeIdAtom : darkThemeIdAtom;
};
export const useThemeMode = (scope?: ThemeScope) => useThemeAtomValue(themeMode, scope);
export const useResolvedThemeMode = (scope?: ThemeScope) => useThemeAtomValue(resolvedThemeModeAtom, scope);
export const useUpdateThemeMode = (scope?: ThemeScope) => useUpdateThemeAtom(themeMode, scope);
export const useCustomTheme = (scope?: ThemeScope) => useThemeAtomValue(customThemeAtom, scope);
export const useCustomThemeAtom = (scope?: ThemeScope) => {
  const mode = useResolvedThemeMode(scope);
  return mode === 'light' ? customLightThemeAtom : customDarkThemeAtom;
};
export const useComputedTheme = (scope?: ThemeScope) => useThemeAtomValue(computedThemeAtom, scope);
export const useThemeIsDark = (scope?: ThemeScope) => {
  const { theme } = useComputedTheme(scope);

  return !!theme.isDark;
};
export const useUpdateTheme = (scope?: ThemeScope) => {
  const customThemeAtom = useCustomThemeAtom(scope);
  return useUpdateThemeAtom(customThemeAtom, scope);
};

/**
 * Light specific
 */

export const lightThemeIdAtom = atom<PrebuiltThemeIds>('default');
export const customLightThemeAtom = atom<ICustomTheme>({ colors: {} });

export const useLightTheme = (scope?: ThemeScope) => useThemeAtomValue(customLightThemeAtom, scope);
export const useLightThemeId = (scope?: ThemeScope) => useThemeAtomValue(lightThemeIdAtom, scope);
export const useUpdateLightThemeId = (scope?: ThemeScope) => useUpdateThemeAtom(lightThemeIdAtom, scope);
export const useUpdateLightTheme = (scope?: ThemeScope) => useUpdateThemeAtom(customLightThemeAtom, scope);

/**
 * Dark specific
 */

export const darkThemeIdAtom = atom<PrebuiltThemeIds>('default_dark');
export const customDarkThemeAtom = atom<ICustomTheme>({ colors: {} });

export const useDarkTheme = (scope?: ThemeScope) => useThemeAtomValue(customDarkThemeAtom, scope);
export const useDarkThemeId = (scope?: ThemeScope) => useThemeAtomValue(darkThemeIdAtom, scope);
export const useUpdateDarkThemeId = (scope?: ThemeScope) => useUpdateThemeAtom(darkThemeIdAtom, scope);
export const useUpdateDarkTheme = (scope?: ThemeScope) => useUpdateThemeAtom(customDarkThemeAtom, scope);

/**
 * Internal helpers
 */

const useThemeAtomValue = <T>(atom: Atom<T>, scope?: ThemeScope) => {
  const currentScope = useContext(ThemeProviderContext);
  const targetScope = scope || currentScope;
  return useAtomValue(atom, targetScope);
};

const useUpdateThemeAtom = <T, U>(atom: WritableAtom<T, U>, scope?: ThemeScope) => {
  const currentScope = useContext(ThemeProviderContext);
  const targetScope = scope || currentScope;
  return useUpdateAtom(atom, targetScope);
};
