import * as React from 'react';

import { useComputedTheme } from '../../hooks';
import { BoxOwnProps } from '../Box';
import { ThemeProvider } from '../ThemeProvider';

export type InvertThemeProps = {
  children: JSX.Element;
  inverted?: boolean;
  propsOnDark?: BoxOwnProps;
};

export const InvertTheme = ({ children, inverted, propsOnDark = {} }: InvertThemeProps) => {
  const { theme, vars } = useComputedTheme();

  if (inverted === false) {
    return children;
  }

  const willInvert = !theme.isDark;

  if (willInvert) {
    return (
      <ThemeProvider theme={{ colors: { background: vars['color-canvas-1000'] } }} isInverted>
        {children}
      </ThemeProvider>
    );
  }

  return React.cloneElement(children, {
    ...propsOnDark,
    style: {
      '--color-canvas': 'var(--color-canvas-tint)',
      ...(children.props.style || {}),
      ...(propsOnDark.style || {}),
    },
  });
};
