import cn from 'clsx';
import memoize from 'nano-memoize';
import equals from 'react-fast-compare';

import type { TCursor, TOpacity, TPointerEvents, TResize, TUserSelect, TVisibility } from '../types/tailwind';
import { ChopPrefix } from '../types/twind';
import { computePseudoClasses, WithPseudo } from './pseudo';
import { EnhancerFn } from './types';

export type Cursor = ChopPrefix<'cursor', TCursor> | boolean;
export type UserSelect = ChopPrefix<'select', TUserSelect>;
export type Resize = ChopPrefix<'resize', TResize>;
export type PointerEvents = ChopPrefix<'pointer-events', TPointerEvents>;
export type OpacityVals = ChopPrefix<'opacity', TOpacity>;
export type VisibilityVals = TVisibility;

export type IInteractivityProps = WithPseudo<{
  cursor?: Cursor;
  opacity?: OpacityVals;
  pointerEvents?: PointerEvents;
  resize?: Resize;
  userSelect?: UserSelect;
  visibility?: VisibilityVals;
}>;

export const interactivityPropNames: Array<keyof IInteractivityProps> = [
  'cursor',
  'opacity',
  'pointerEvents',
  'resize',
  'userSelect',
  'visibility',
];

export const interactivityProps: EnhancerFn<IInteractivityProps> = (props: IInteractivityProps) => {
  const { cursor, userSelect, pointerEvents, opacity, resize, visibility, ...rest } = props;

  return {
    props: rest,
    className: _interactivityProps(cursor, userSelect, pointerEvents, opacity, resize, visibility),
  };
};

const _interactivityProps = memoize(
  (
    cursor: IInteractivityProps['cursor'],
    userSelect: IInteractivityProps['userSelect'],
    pointerEvents: IInteractivityProps['pointerEvents'],
    opacity: IInteractivityProps['opacity'],
    resize: IInteractivityProps['resize'],
    visibility: IInteractivityProps['visibility'],
  ) => {
    return cn(
      computePseudoClasses('cursor', cursor),
      computePseudoClasses('select', userSelect),
      computePseudoClasses('pointer-events', pointerEvents),
      computePseudoClasses('resize', resize),
      // the visibility value itself is the class, no need to pass name
      computePseudoClasses('', visibility),
      computePseudoClasses('opacity', opacity),
    );
  },
  { maxAge: Infinity, equals },
);
