import { OverlayProvider as ReactAriaOverlayProvider } from '@react-aria/overlays';
import { SSRProvider as ReactAriaProvider } from '@react-aria/ssr';
import cn from 'clsx';
import * as React from 'react';
import { CSSProperties, useRef } from 'react';

import { defaultMosaicContext, MosaicContext, MosaicContextProps, useId, useMosaicContext } from '../../hooks';
import { preflightCss } from '../../utils';
import { Box } from '../Box';
import { ThemeProvider } from '../ThemeProvider';
import { styles } from './styles';

export type ProviderProps = Partial<MosaicContextProps> & {
  children: React.ReactNode;
  className?: string;
  style?: CSSProperties;
  skipPreflight?: boolean;
};

export const rootThemeScope = Symbol('rootTheme');

function MosaicProvider({ children, ...props }: ProviderProps) {
  try {
    // if we're already in a mosaic provider context, we do not need to create a new one
    // wrap in a fragment so that a JSX.Element typing is returned
    const ctx = useMosaicContext();

    // will have an assigned providerId if already present in the tree
    if (ctx.providerId) {
      return <>{children}</>;
    }
  } catch {}

  return (
    <ReactAriaProvider>
      <MosaicInnerProvider {...props}>{children}</MosaicInnerProvider>
    </ReactAriaProvider>
  );
}

function MosaicInnerProvider({ children, className, style, skipPreflight, ...mosaicContext }: ProviderProps) {
  const providerId = `mosaic-provider-${useId()}`;
  const providerRef = useRef<HTMLDivElement>();

  return (
    <ThemeProvider ref={providerRef} scope={rootThemeScope}>
      <Box
        className={className}
        style={style}
        styled={[styles, skipPreflight ? undefined : preflightCss]}
        id={providerId}
      >
        <ReactAriaOverlayProvider className={cn(className)} style={style}>
          <MosaicContext.Provider
            value={Object.assign({}, defaultMosaicContext, mosaicContext, { providerId, providerRef })}
          >
            {children}
          </MosaicContext.Provider>
        </ReactAriaOverlayProvider>
      </Box>
    </ThemeProvider>
  );
}

export { MosaicProvider as Provider };
