import cn from 'clsx';
import memoize from 'nano-memoize';
import equals from 'react-fast-compare';

import type { TDisplay, TObjectFit, TObjectPosition } from '../types/tailwind';
import type { ChopPrefix } from '../types/twind';
import { computePseudoClasses, WithPseudo } from './pseudo';
import type { EnhancerFn } from './types';

export type DisplayVals = TDisplay;
export type OverflowVals = 'auto' | 'scroll' | 'hidden' | 'visible' | 'clip';
export type ObjectFitVals = ChopPrefix<'object', TObjectFit>;
export type ObjectPositionVals = ChopPrefix<'object', TObjectPosition>;

export type ILayoutProps = WithPseudo<{
  display?: DisplayVals;
  overflowY?: OverflowVals;
  overflowX?: OverflowVals;
  objectFit?: ObjectFitVals;
  objectPosition?: ObjectPositionVals;
}>;

export const layoutPropNames: Array<keyof ILayoutProps> = [
  'display',
  'overflowX',
  'overflowY',
  'objectFit',
  'objectPosition',
];

export const layoutProps: EnhancerFn<ILayoutProps> = props => {
  const { display, overflowX, overflowY, objectFit, objectPosition, ...rest } = props;

  return { props: rest, className: _layoutProps(display, overflowX, overflowY, objectFit, objectPosition) };
};

const _layoutProps = memoize(
  (
    display: ILayoutProps['display'],
    overflowX: ILayoutProps['overflowX'],
    overflowY: ILayoutProps['overflowY'],
    objectFit: ILayoutProps['objectFit'],
    objectPosition: ILayoutProps['objectPosition'],
  ) => {
    return cn(
      // the display value itself is the class, no need to pass name
      computePseudoClasses('', display),
      computePseudoClasses('overflow-x', overflowX),
      computePseudoClasses('overflow-y', overflowY),
      computePseudoClasses('object', objectFit),
      computePseudoClasses('object', objectPosition),
    );
  },
  { maxAge: Infinity, equals },
);
