import { CX, twCss, twStyle } from '../../utils';

export const badgeStyle = twStyle({
  base: twCss({
    '@apply': CX('inline-flex', 'items-center', 'outline-none', 'border'),
    '& a': {
      '@apply': CX.$combine(CX('text-muted'), CX.hover('hover:text-body', 'hover:cursor-pointer')),
    },
  }),
});
