import { CX, twCss, twStyle } from '../../utils';

export const menuStyle = twStyle({
  base: `select-none`,
  props: {
    /* only apply these pointer styles when user is interacting with a mouse */
    withInteractions: {
      true: twCss({
        '.sl-menu-item:not(.sl-menu-item--disabled):hover': {
          '@apply': CX('bg-primary', 'text-on-primary'),

          '& .sl-menu-item__icon': {
            // @ts-expect-error
            '@apply': CX('text-on-primary!'),
          },

          '& .sl-menu-item__description': {
            // @ts-expect-error
            '@apply': CX('text-on-primary!'),
          },
        },
      }),
    },
  },
});

export const menuItemStyle = twStyle({
  base: twCss({
    '.sl-menu-item__title-wrapper': {
      'max-width': '250px',
    },

    '.sl-menu-item__description': {
      display: '-webkit-box',
      '-webkit-line-clamp': 2,
      '-webkit-box-orient': 'vertical',
      overflow: 'hidden',
    },
  }),

  props: {
    isFocused: {
      true: twCss({
        '@apply': CX('bg-primary', 'text-on-primary'),

        '.sl-menu-item__link-icon, .sl-menu-item__meta': {
          '@apply': CX('opacity-100'),
        },

        '.sl-menu-item__icon': {
          // @ts-expect-error
          '@apply': CX('text-on-primary!'),
        },

        '.sl-menu-item__description': {
          // @ts-expect-error
          '@apply': CX('text-on-primary!'),
        },
      }),
    },

    submenuActive: {
      true: CX('bg-primary-tint'),
    },
  },
});
