import cn from 'clsx';
import memoize from 'nano-memoize';
import equals from 'react-fast-compare';

import type {
  TFontFamily,
  TFontSize,
  TFontStyle,
  TFontWeight,
  TLetterSpacing,
  TLineHeight,
  TTextAlign,
  TTextDecoration,
  TTextOverflow,
  TTextTransform,
  TVerticalAlign,
  TWhitespace,
  TWordBreak,
} from '../types/tailwind';
import type { ChopPrefix } from '../types/twind';
import { computePseudoClasses, WithPseudo } from './pseudo';
import type { EnhancerFn } from './types';

export type FontSizeVals = ChopPrefix<'text', TFontSize>;

export type LineHeights = ChopPrefix<'leading', TLineHeight>;
export type LetterSpacing = ChopPrefix<'tracking', TLetterSpacing>;

export type FontFamilyVals = ChopPrefix<'font', TFontFamily>;
export type FontWeightVals = ChopPrefix<'font', TFontWeight>;
export type FontStyleVals = TFontStyle;
export type TextAlign = ChopPrefix<'text', TTextAlign>;
export type TextDecoration = TTextDecoration;
export type TextTransform = TTextTransform;
export type TextOverflow = TTextOverflow;
export type VerticalAlign = ChopPrefix<'align', TVerticalAlign>;
export type Whitespace = ChopPrefix<'whitespace', TWhitespace>;
export type WordBreak = ChopPrefix<'break', TWordBreak>;

export type ITypographyProps = WithPseudo<{
  fontSize?: FontSizeVals;
  lineHeight?: LineHeights;
  letterSpacing?: LetterSpacing;
  fontFamily?: FontFamilyVals;
  fontWeight?: FontWeightVals;
  fontStyle?: FontStyleVals;
  textAlign?: TextAlign;
  textDecoration?: TextDecoration;
  textTransform?: TextTransform;
  textOverflow?: TextOverflow;
  verticalAlign?: VerticalAlign;
  whitespace?: Whitespace;
  wordBreak?: WordBreak;
}>;

export const typographyPropNames: Array<keyof ITypographyProps> = [
  'fontFamily',
  'fontSize',
  'fontStyle',
  'fontWeight',
  'letterSpacing',
  'lineHeight',
  'textAlign',
  'textDecoration',
  'textOverflow',
  'textTransform',
  'verticalAlign',
  'whitespace',
  'wordBreak',
];

export const typographyProps: EnhancerFn<ITypographyProps> = (props: ITypographyProps) => {
  const {
    fontSize,
    lineHeight,
    letterSpacing,
    fontFamily,
    fontWeight,
    fontStyle,
    textAlign,
    textDecoration,
    textTransform,
    textOverflow,
    verticalAlign,
    whitespace,
    wordBreak,
    ...rest
  } = props;

  return {
    props: rest,
    className: _typographyProps(
      fontSize,
      lineHeight,
      letterSpacing,
      fontFamily,
      fontWeight,
      fontStyle,
      textAlign,
      textDecoration,
      textTransform,
      textOverflow,
      verticalAlign,
      whitespace,
      wordBreak,
    ),
  };
};

const _typographyProps = memoize(
  (
    fontSize: ITypographyProps['fontSize'],
    lineHeight: ITypographyProps['lineHeight'],
    letterSpacing: ITypographyProps['letterSpacing'],
    fontFamily: ITypographyProps['fontFamily'],
    fontWeight: ITypographyProps['fontWeight'],
    fontStyle: ITypographyProps['fontStyle'],
    textAlign: ITypographyProps['textAlign'],
    textDecoration: ITypographyProps['textDecoration'],
    textTransform: ITypographyProps['textTransform'],
    textOverflow: ITypographyProps['textOverflow'],
    verticalAlign: ITypographyProps['verticalAlign'],
    whitespace: ITypographyProps['whitespace'],
    wordBreak: ITypographyProps['wordBreak'],
  ) => {
    return cn(
      computePseudoClasses('text', fontSize),
      computePseudoClasses('leading', lineHeight),
      computePseudoClasses('tracking', letterSpacing),
      computePseudoClasses('font', fontFamily),
      computePseudoClasses('font', fontWeight),
      computePseudoClasses('text', textAlign),

      // the props below have a value that itself is the class, no need to pass name
      computePseudoClasses('', fontStyle),
      computePseudoClasses('', textDecoration),
      computePseudoClasses('', textTransform),
      computePseudoClasses('', textOverflow),

      computePseudoClasses('align', verticalAlign),
      computePseudoClasses('whitespace', whitespace),
      computePseudoClasses('break', wordBreak),
    );
  },
  { maxAge: Infinity, equals },
);
