import { createContext, useContext } from 'react';

const DEFAULT_CONTEXT: InternalMosaicContextProps = { componentOverrides: {} };

export type MosaicContextProps = {
  componentOverrides: {
    Link?: React.ComponentType<React.AnchorHTMLAttributes<HTMLAnchorElement>>;
    Image?: React.ComponentType<React.ImgHTMLAttributes<HTMLImageElement>>;
  };
};

export const defaultMosaicContext: MosaicContextProps = { componentOverrides: {} };

export type InternalMosaicContextProps = {
  providerId?: string;
  providerRef?: React.RefObject<HTMLDivElement>;
} & MosaicContextProps;

export const MosaicContext = createContext<InternalMosaicContextProps | undefined>(undefined);

export const useMosaicContext = () => {
  return useContext(MosaicContext) || DEFAULT_CONTEXT;
};
